<template>
	<div class="homePage" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
		<div>
			<template v-if='swipeItem.length'>
				<mt-swipe class="homePageswipe">
					<mt-swipe-item v-for='(item,$index) in swipeItem' :key='$index'>
						<img :src="item">
					</mt-swipe-item>
				</mt-swipe>
			</template>
			<img src="../../assets/images/homepage/userBtn.png" class='userBtn' @click='gotoUser'>
			<div class="btnCon">
        <div>
          <div class="btn" @click="goToPage('rendezvous')">
            <span>
              <img src="../../assets/images/homepage/btnIcon1.png">
              <span>约</span>
            </span>
          </div>
          <div class="btn" @click="goToPage('course')">
            <span>
              <img src="../../assets/images/homepage/btnIcon2.png">
              <span>课程</span>
            </span>
          </div>
          <div class="btn" @click="goToPage('equip')">
            <span>
              <img src="../../assets/images/homepage/btnIcon3.png">
              <span>装备</span>
            </span>
          </div>
        </div>
        <div>
          <div class="btn" @click="goToPage('seeOcean')">
            <span>
              <img src="../../assets/images/homepage/btnIcon4.png">
              <span>看海洋</span>
            </span>
          </div>
          <div class="btn" @click="goToPage('forumPage')">
            <span>
              <img src="../../assets/images/homepage/btnIcon5.png" alt="" srcset="">
              <span>社区</span>
            </span>
          </div>
          <div class="btn">
            <span>
              <img src="../../assets/images/homepage/btnIcon6.png" alt="" srcset="">
              <span>商店</span>
            </span>
          </div>
        </div>
			</div>
			<hr />
			<div class="hotTravel">
				<div class="hotTravelTitle">热门行程</div>
				<v-rendezvousPage :Lists='Lists' />
			</div>
			<div class="loadmoreTips" v-if="!allLoaded&&isLoading">
				<img src="../../assets/images/loading.gif" alt=""> 加载中...
			</div>
			<div class="loadmoreTips" v-if='allLoaded'>已经加载到底部啦^_^</div>
		</div>
	</div>
</template>
<script>
	import rendezvousPage from '../rendezvous/rendezvousList.vue'
	export default {
		name: '',
		components: {
			'v-rendezvousPage': rendezvousPage,
		},
		props: {},
		data() {
			return {
				isLoading:false,
				allLoaded: false,
				swipeItem:[],
				Lists: []
			}
		},
		created(){
			this.getHomepageInfo();
		},
		methods: {
			getHomepageInfo(){
				this.$http.get(this.$port.HOMEPAGE_PORT,{})
				.then((response) => {
						const {data} = response;
            console.log(data)
						if(response.code == 200){
							this.swipeItem=data.banner.split('#');
							this.Lists = data.hotList;
              this.allLoaded = true;
						}
					})
					.catch((error) => {
						console.log(error.response.data);
					});
			},
			loadMore() {
        if(this.Lists.length>0&&!this.allLoaded){
				  this.Lists.push(this.Lists[0])
        }
			},
			gotoUser() {
				this.$router.push('/personalHomePage');
			},
			goToPage(data) {
				switch (data) {
					case 'rendezvous':
						this.$router.push('/rendezvousPage/3');
						break;
					case 'course':
						this.$router.push('/rendezvousPage/5');
						break;
					case 'equip':
						this.$router.push('/rendezvousPage/4');
						break;
					case 'seeOcean':
						this.$router.push('/seeOcean');
						break;
					case 'forumPage':
						this.$router.push('/forumPage');
						break;
					default:
						break;
				}
			}
		}
	}
</script>
<style lang="scss">
	.mint-swipe-items-wrap img {
		width: 100%;
		// height: 100%;
	}

	.mint-swipe-indicators {
		bottom: 0.2rem;
		line-height: 0;
		font-size: 0;
		.mint-swipe-indicator {
			opacity: 1;
			width: 0.12rem;
			height: 0.12rem;
			margin: 0 0.14rem;
			background: rgba(255, 255, 255, 0.3);
			&.is-active {
				background: white
			}
		}
	}
</style>

<style lang="scss" scoped>
	* {
		border: none;
		margin: 0;
		padding: 0;
	}

	.userBtn {
		position: absolute;
		top: 0.25rem;
		right: 0.5rem;
		width: 0.48rem;
	}

	.homePage {
		position: absolute;
		top: 0;
		left: 0;
		right:0;
		bottom:0;
		margin:auto;
		background: white;
		font-size: 0.32rem;
		color: #555555;
		overflow-x: hidden;
    	overflow-y: scroll;
    	-webkit-overflow-scrolling: touch;
		.homePageswipe {
			width: 7.5rem;
			height: 4.1rem;
		}
		.btnCon {
			width: 100%;
			height: 4.28rem;
      &>div{
        width: 100%;
			  height: 50%;
        display: flex;
        flex-flow: row wrap;
        .btn {
          border-bottom: 0.01rem solid rgb(229, 229, 229);
          border-right: 0.01rem solid rgb(229, 229, 229);
          flex:1;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 0.32rem;
          text-align: center;
          color: #555555;
          img {
            display: block;
            width: 0.72rem;
            margin-bottom: 0.26rem;
          }
        }
      }
		}
		hr {
			width: 100%;
			height: 0.2rem;
			background: #f2f2f2;
		}
		.hotTravel {
			width: 100%;
			.hotTravelTitle {
				width: 6.9rem;
				margin: 0.38rem 0.3rem -0.2rem 0.3rem;
			}
		}
		.loadmoreTips {
			font-size: 0.28rem;
			padding: 0.28rem;
			align-items: center;
			display: flex;
			justify-content: center;
		}
		.loadmoreTips>img {
			width: 0.5rem;
		}
	}
</style>



